#enter{
  width: 150px;
  height: 35px;
  border: double 4px transparent;
  border-radius: 80px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(white), to(white)), -webkit-gradient(linear, left top, right top, from(#00b09b), to(#00b09b));
  background-image: -webkit-linear-gradient(white, white), -webkit-linear-gradient(left, #00b09b, #00b09b);
  background-image: -o-linear-gradient(white, white), -o-linear-gradient(left, #00b09b, #00b09b);
  background-image: linear-gradient(white, white), linear-gradient(to right, #00b09b, #00b09b);
  background-origin: border-box;
  background-clip: content-box, border-box;
  text-align: center;
  line-height: 35px;
  -webkit-transition:.2s;
  -o-transition:.2s;
  transition:.2s;
  cursor: pointer;
  letter-spacing: 0px;
  font-size: 20px;
  font-family: 'helvetica';
  overflow: hidden;
  margin-top:-50px;
  position: absolute;

}
#enter:hover{
    background-image: -webkit-gradient(linear, left top, left bottom, from(white), to(white)), -webkit-gradient(linear, left top, right top, from(#00b09b), to(#96c93d));
    background-image: -webkit-linear-gradient(white, white), -webkit-linear-gradient(left, #00b09b, #96c93d);
    background-image: -o-linear-gradient(white, white), -o-linear-gradient(left, #00b09b, #96c93d);
    background-image: linear-gradient(white, white), linear-gradient(to right, #00b09b, #96c93d);
    -webkit-transform:translateY(-3px);
        -ms-transform:translateY(-3px);
            transform:translateY(-3px)
}


